.App {
  text-align: center;
  min-height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  flex-direction: column;
  margin-top: 50px;
  padding-bottom: 60px;
}
.notes-list {
  /* margin-top: 9em; */
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.writeNotes {
  font-weight: normal;
  margin-bottom: 100px;
  font-size: 50px;
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande',
    'Lucida Sans', Arial, sans-serif;
}
